    import {  
        mapState,  
        mapMutations  
    } from 'vuex';  
    export default {  
		data() {
			return {
				predepoit:'',
				txwayinfo:[],
				txmoney:'',
				logining: false,				
				password: '',
				mask: false,
				passwordArray: [],
				pwdnum:0,
				bott: '',
				pasList: ['', '', '', '', '', ''],
				numbr: [1, 2, 3, 4, 5, 6, 7, 8, 9]
			}				
		},
        computed: mapState(['sellerHasLogin']),
		onLoad() {
			 console.log('页面加载');
			this.loadData();
		},
        methods: {  
			...mapMutations(['setUserInfo']),
            async loadData() {  
				let _this=this;
                if (!this.sellerHasLogin) {  
					uni.navigateTo({
						url:'../login',
					})
                }
				
				_this.$Request.post('seller_fund/gettxway').then(res => {
					if (res.code == 200) {
						console.log(res);
						_this.predepoit = res.datas.predepoit;
						_this.txwayinfo = res.datas.txway;
						if(res.datas.notxway==1){
							uni.showToast({
								icon: 'none',
								title:'请先添加提现方式！',
								duration:2000,
								success: function () {
									 setTimeout(function () {
										uni.navigateTo({
											url:'txwayadd',
										});
									 }, 2000);
								}
							});
						}
					}
				}).catch(function (err) {
					console.log(err);
				  });
				  
            },
			formSubmit: function () {
				var _this = this;
				if(_this.txmoney ==''||parseFloat(_this.txmoney)<0.01){ 
					uni.showToast({title: '请填写正确的提现金额',icon:"none"});
					return false; 
				}
				if(parseFloat(_this.txmoney)>_this.predepoit){ 
					uni.showToast({title: '提现金额不足',icon:"none"});
					return false; 
				}
				_this.logining = true;
				let postData = {id:_this.txwayinfo.id,pdc_amount:_this.txmoney,ppwd:_this.password};
				_this.$Request.post('seller_fund/pd_cash_add',postData).then(res => {
					if (res.code == 200) {
						console.log(res);
						uni.showToast({
							icon: 'none',
							title:'提交成功！',
							duration:3000,
							success: function () {
								 setTimeout(function () {
									uni.navigateTo({
										url:'/pages/seller/seller',
										success (res) { 
											let page = getCurrentPages().pop(); 
											if (page == undefined || page == null) { 
											  return ;
											 } 
											 page.onLoad() ;
										}
									});
								 }, 2000);
							}
						});
					}
					else{
						uni.showToast({
							icon: 'none',
							title:res.datas.error,
							duration:2000
						});
						_this.logining = false;
					}
				}).catch(function (err) {
					console.log(err);
				  });
			},
			toPage: function(url) {
				uni.navigateTo({
					url:url,
				})
			},
			// 密码框
			checkNum(num) {
				let _this = this;
				if (this.pwdnum < 6) {
					console.log('---'+num);
					this.passwordArray.push(num+'');
					this.pwdnum++;
					//this.passwordArray=[this.passwordArray,num];
					console.log(this.passwordArray);
					
				}
				if (this.pwdnum == 6) {
					let str = '';
					for (let i = 0; i < this.passwordArray.length; i++) {
						str += this.passwordArray[i];
					}
					console.log(str)
					this.password = str
					this.mask = false
					this.passwordArray = []
					this.bott = '';
					console.log(this.password);
					
					_this.formSubmit();
					
					this.pwdnum=0;
				}
			},
			reset() {
				this.passwordArray = [];
				this.pwdnum=0;
			},
			backspace() {
				this.passwordArray.pop();
				this.pwdnum--;
			},
			masks() {
				let _this = this
				
				if(_this.txmoney ==''||parseFloat(_this.txmoney)<0.01){ 
					uni.showToast({title: '请填写正确的提现金额',icon:"none"});
					return false; 
				}
				if(parseFloat(_this.txmoney)>_this.predepoit){ 
					uni.showToast({title: '提现金额不足',icon:"none"});
					return false; 
				}
				this.mask = true
				this.pwdnum=0;
				setTimeout(()=> {
					_this.bott = 'bot'
				}, 50)
			},
			maskss() {
				this.mask = false
				this.bott = ''
				this.passwordArray = []
				this.pwdnum=0;
			},
			 
        }  
    }  
